.wpe-dropdown-container {

    width: 300px;
    margin-right: 10px;

    &:last-child {
        margin-right: 0;
    }

    .wpe-dropdown-wrapper {

        position: relative;
        border: 1px solid #ddd;
    
        .wpe-dropdown-header {
    
            position: relative;
            background: white;
            height: 34px;
            cursor: pointer;
    
            .wpe-dropdown-header-title {
                padding: 8px 14px 0 14px;
            }
    
            .dashicon {
    
                position: absolute;
                top: 8px;
                right: 5px;
            }
        }
    
        .wpe-dropdown-list {
    
            position: absolute;
            z-index: 2;
            background: white;
            border: 1px solid #ddd;
            top: 34px;
            left: -1px;
            right: -1px;
            padding: 10px;
            box-shadow: 0px 10px 25px rgb(0 0 0 / 15%);
    
            .wpe-dropdown-list-item-parent {
    
                margin-bottom: 10px;
    
                &:last-child {
                    margin-bottom: 0;
                }
    
                .wpe-dropdown-list-item-cat {
    
                    color: rgb(160,160,160);
                }
    
                .wpe-dropdown-list-item-children-list {
                    padding: 0px 10px;
                }
            }
    
            .wpe-dropdown-list-item {
                padding: 4px;
                border-bottom: 1px solid rgb(240 240 240);
                cursor: pointer;
    
                &:last-child {
                    border-bottom: none;
                }
    
                &:hover {
                    background: rgb(240 240 240);
                }
            }
        }
    }
}
